@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;500&display=swap');
* {
	margin: 0;
	padding: 0;
}
body {
	background-color: #fff;
}
a {
	text-decoration: none;
	color: #333;
	transition: color 2s;
}
a:hover {
	color: #ff00d4;
	transition: color 0.25s;
}
.App {
	text-align: center;
	cursor: default;
}

header {
	display: flex;
	flex-direction: column;
	align-items: center;
	color: #333;
	margin-top: 25vh;
	margin-bottom: 5vh;
	font-size: max(2em, 54px);
}
header h1 {
	font-weight: 500;
	letter-spacing: calc(2px * 2vmin);
	margin: 0;
	padding: 0;
	display: inline-block;
	opacity: 1;
	transition: opacity 2s;
}
header h1 .small {
	font-weight: 150;
	font-size: 0.5em;
}
header h1:hover {
	opacity: 0.8;
	transition: opacity 0.5s;
}
header h1:hover .rotate {
	color: #ff79e9;
	transition: color 0.5s;
}
header h2.slogan {
	font: 1rem;
	color: #ccc;
	padding: 0;
	margin: -0.7em 0 0 0;
	letter-spacing: 0.065em;
	text-transform: uppercase;
	font-size: 0.24em;
	font-weight: 300;
}

header h1 .reverse {
	display: inline-block;
	position: relative;
	-moz-transform: scale(-1, 1);
	-webkit-transform: scale(-1, 1);
	-o-transform: scale(-1, 1);
	-ms-transform: scale(-1, 1);
	transform: scale(-1, 1);
}

header h1 .rotate {
	visibility: visible;
	font-size: 1em;
	position: relative;
	display: inline-block;
	margin: 0 0.072em 0 -0.072em;
	padding: 0;
	transform: rotate(-34.5deg);
	transition: color 2s;
}
main {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: justify;
	line-height: 1.4em;
	font-size: 1rem;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
		Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	font-weight: 200;
	color: #666;
}
main p {
	width: 100%;
	max-width: 22em;
	line-height: 1.6em;
	padding: 0 0 1.2em;
	background-color: transparent;
}
main p:nth-child(1) {
	font-weight: 500;
	text-align: center;
	padding-bottom: 5vh;
}

main p:nth-last-child(1) {
	text-align: end;
}
img {
	opacity: 0.2;
	transition: opacity 2s;
	cursor: pointer;
}
img:hover {
	opacity: 0.8;
	transition: opacity 0.5s;
}
.center {
	text-align: center !important;
}
